
  import React from 'react';
  import '../Other/Other.css';


  const Publications = function(props){

    var data_pubs = [
      {
        img: "img/pubs/pkclustering.png",
        title: "Integrating Prior Knowledge in Mixed Initiative Social Network Clustering",
        authors: "Pister, A., Buono, P., Fekete, J. D., Plaisant, C. & Valdivia, P.",
        booktitle: "preprint",
        year: "2020",
        link: "https://arxiv.org/abs/2005.02972",
        project_page: "https://www.aviz.fr/Research/Pkclustering",
        demo: "http://paovis.ddns.net/paoh.html"
      }, {
        img: "img/paohvis.png",
        title: "Analyzing Dynamic Hypergraphs with Parallel Aggregated Ordered Hypergraph Visualization",
        authors: "Valdivia, P., Buono, P., Plaisant, C., Dufournaud, N., & Fekete, J. D.",
        booktitle: "IEEE transactions on visualization and computer graphics",
        year: "2019",
        pdf: "https://hal.inria.fr/hal-02264960/document",
        gitlab: "https://gitlab.inria.fr/aviz/paohvis.git",
        link: "https://10.1109/TVCG.2019.2933196",
        project_page: "https://www.aviz.fr/paohvis",
        demo: "http://paovis.ddns.net/paoh.html"
      },
      {
        img: "img/pubs/minnesota.png",
        title: "Wavelet-Based visual analysis for data exploration",
        authors: "Dal Col, A., Valdivia, P., Petronetto, F., Dias, F., Silva, C. T., & Nonato, L. G",
        booktitle: "Computing in Science & Engineering",
        year: "2017",
        volume: "19",
        number: "5",
        pages: "pp. 85-91",
        pdf: "files/pubs/wavexp.pdf",
        link: "https://doi.org/10.1109/MCSE.2017.3421553"
      },{
        img: "img/pubs/signal_coefs.png",
        title: "Wavelet-based visual analysis of dynamic networks",
        authors: "Dal Col, A., Valdivia, P., Petronetto, F., Dias, F., Silva, C. T., & Nonato, L. G",
        booktitle: "IEEE transactions on visualization and computer graphics",
        year: "2017",
        pdf: "files/pubs/dynwav_tvcg2017.pdf",
        link: "https://doi.org/10.1109/TVCG.2017.2746080",
        git: "https://github.com/paolavaldivia/dynnet_wavelet.git",
        demo: "https://paolavaldivia.github.io/dynnet_wavelet/"
      },
      {
        img: "img/pubs/pipeline_wav.png",
        title: "Wavelet-based visualization of time-varying data on graphs",
        authors: "P. Valdivia, F. Dias, F. Petronetto, C. T. Silva and L. G. Nonato",
        booktitle: "Visual Analytics Science and Technology (VAST), 2015 IEEE Conference on, Chicago, IL",
        year: "2015",
        git: "https://github.com/paolavaldivia/waviz",
        demo: "https://paolavaldivia.github.io/waviz/",
        pdf: "files/pubs/waviz_vast2015.pdf",
        link: "http://dx.doi.org/10.1109/VAST.2015.7347624"
      },{
        img: "img/pubs/hyper.png",
        title: "Watersheds on hypergraphs for data clustering",
        authors: "Dias, F., Mansour, M. R., Valdivia, P., Cousty, J., & Najman, L.",
        booktitle: "International Symposium on Mathematical Morphology and Its Applications to Signal and Image Processing",
        pages: "pp. 211-221",
        year: "2017",
        git: "https://github.com/fabioasdias/hypershed.git",
        pdf: "files/pubs/hyper_morphology2017.pdf",
        link: "https://doi.org/10.1007/978-3-319-57240-6_17"
      },
      {
        img: "img/pubs/pipe_bil.png",
        title: "Normal correction towards smoothing point-based surfaces ",
        authors: "P. Valdivia, D. Cedrim, F. Petronetto, A. Paiva and L.G. Nonato",
        booktitle: "Proceedings of the 2013 XXVI Conference on Graphics, Patterns and Images",
        pages: "pp. 187-194",
        year: "2013",
        pdf: "files/pubs/normal_sib13.pdf",
        link: "http://dx.doi.org/10.1109/SIBGRAPI.2013.34"
      }
    ];

    var publications = data_pubs.map((pub, i) => {
      return(
        <span key={i}>
          <div className="publication d-flex flex-column flex-sm-row pb-4">
            <div className="figure flex-shrink-1 w-auto pb-0 pb-sm-2">
              <a href={pub.demo || pub.pdf}>
                {pub.img && <img alt={pub.title} src={pub.img} /> }
              </a>
            </div>
            <div className="ref w-100 w-sm-75 pl-0 pl-sm-3 pt-3 pt-sm-0 flex-shrink-0 flex-grow-1">
              <span className="ptitle"> <a href={pub.link}> {pub.title} </a> </span>
              <span className="authors"> {pub.authors} </span>
              <span className="booktitle"> {pub.booktitle}, </span>
              {pub.pages && <span className="pages"> {pub.pages}, </span>}
              <span className="year"> {pub.year}.</span>
              {pub.pdf && <span className="document"> <a href={pub.pdf}> <img alt="pdf" src="img/pdf.png" className="icon" /> </a> </span>}
              {pub.link && <span className="document"> <a href={pub.link}> <img alt="doi" src="img/doi.png" className="icon" /> </a></span>}
              {pub.demo && <span className="document"> <a href={pub.demo}> <img alt="link" src="img/external.png" className="icon" /> </a> </span>}
              {pub.project_page && <span className="document"> <a href={pub.project_page}> <img alt="link" src="img/external.png" className="icon" /> </a> </span>}
              {pub.git && <span className="document"> <a href={pub.git}> <img alt="pdf" src="img/github.png" className="icon" /> </a> </span>}
              {pub.gitlab && <span className="document"> <a href={pub.gitlab}> <img alt="pdf" src="img/gitlab.png" className="icon" /> </a> </span>}
            </div>
          </div>
          <div className="space"/>
        </span>
      );
    });

    return (
    <div id="pubs" className="publications">

      <div className="space"/>
      <h2>
        PUBLICATIONS
      </h2>
      <div className="space"/>


      {publications}

      <div className="space-lg"/>

    </div>
  );
}

export default Publications;



