import React from 'react';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import Home from "../Home/Home";
import Projects from "../Projects";
import './App.css';
import Contact from "../Contact/Contact";


export default function App() {
  return (
      <Router>
        <div>
          <div className="navbar navbar-expand-lg navbar-default" role="navigation">
            <div className="container">
              <div id="nav">
                <ul className="navbar-nav navbar-center">
                  <li className="nav-item active"><a href="/">ABOUT ME</a></li>
                  <li className="nav-item"><a href="/#pubs">PUBLICATIONS</a></li>
                  <li className="nav-item"><Link to="/projects">PROJECTS</Link></li>
                  <li className="nav-item"><a href="/#other">OTHER</a></li>
                </ul>
              </div>
            </div>
          </div>

          {/* A <Switch> looks through its children <Route>s and
            renders the first one that matches the current URL. */}
          <Switch>
            <Route path="/projects">
              <Projects />
            </Route>
            <Route path="/">
              <Home />
            </Route>
          </Switch>

          <footer>
            {/*<Contact />*/}
            paola.valdivia@inria.fr
            <br/>
            last update: Jan, 2021 &nbsp;&nbsp;&nbsp;&nbsp;
            ©  <a href="#about"> Paola Valdivia </a>
          </footer>
        </div>
      </Router>
  );
}



